* {
    box-sizing: border-box;
}

html,body
{
  height: 100%;
  width: 100%;
  margin:0;
  padding:0;
}

body
{
  font-family: 'Open Sans',sans-serif;
  //font-weight:300;
  font-size: $size-default;
  line-height:1.5;
  background:$soft-white;
}

a {
  text-decoration:none;
  color:$green;
}

img
{
  width:100%;
  height:auto;
  max-width:100%;
}

h1,h2
{
  font-weight: 300;
}

h1,h2
{
  margin-top:0;
  margin-bottom: $space-1;
  padding:0;
  line-height:1.2;
}

h1 { font-size: $size-1; }
h2 { font-size: $size-2; }
h3 { font-size: $size-3; }
h4 { font-size: $size-4; }
h5 { font-size: $size-5; }
h6 { font-size: $size-6; }
